'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import ButtonEntry from "@/app/_components/buttonentry";
import Feedback from "@/app/_components/feedback";
import BasicTable from "@/app/_components/basictable";

let submitObject = {};

const initState = {
    'LSManager Account':{
        value:''
    },

    'Submit':{
        Color:'primary',
        Text:'Submit'
    },
    'Select Asset':{
        options: [],
        value:0
    },
    'Registrations':[{}],
    'Feedback':{
        color:'success',
        code:'',
        title:'',
        text:'',
        hidden: true
    }
};


const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Value':{

            newState[action.label].value = action.value;

            newState.Submit.Color = 'primary';
            newState.Submit.Text = 'Submit';
            newState.Feedback.hidden = true;

            return newState

            break
        }

        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;

        }
        
        case 'load-Options':{

            //console.dir(action.data);

            let courses = action.data.getCourses;

            newState['Select Asset'].options = courses;

            newState['Select Asset'].value = courses[0].ID;

            return newState

            break;
        }

        case 'load-Report':{

            let students = action.report;

            newState['Registrations'] = students;

            return newState

            break;
        }

         case 'load-SelectBox':{

            let Sessions = action.data.Sessions;

            newState['Select Asset'].options = Sessions;

            newState['Select Asset'].value = Sessions[0].ID;

            return newState

            break;

        }
        case 'load-Feedback':{

            newState['Feedback'].hidden = false;
            newState['Feedback'].color = action.color;
            newState['Feedback'].code = action.code;
            newState['Feedback'].title = action.title;
            newState['Feedback'].text = action.text;

            return newState;

            break;

        }
        

    }
};

export default function PlanIssues(){


    const [state, dispatch] = useReducer(reducer,initState);

        useEffect(function(){

            const loadDropListOptions = async()=>{

                let options = await fetch({method:'GET',endpoint:'Litmos/RetrieveNHAssets'});

                if(options.didError){

                    //Do stuff

                    return

                }

                dispatch({type:'load-SelectBox',data:options.data})

            }

            loadDropListOptions();
        
            return

        },[]
    );

    submitObject = {
    AssetID: state['Select Asset'].value}


    return (

        <main className={styles.main} style={{backgroundImage: `url(${backgroundImage.src})`}}>
            
          <Form>
            <Select
                options={state['Select Asset'].options}
                label='Select Asset'
                value={state['Select Asset'].value}
                dispatch={dispatch}
            >
            </Select>
            <Submit 
                method="POST"
                endpoint="Litmos/RetrieveIssuesInPlan"
                dispatch={dispatch}
                data={submitObject}
                color={state.Submit.Color}
                text={state.Submit.Text}
            ></Submit>
            <BasicTable
                records={state['Registrations']}
            >
            </BasicTable>
            <Feedback
                color={state['Feedback'].color}
                code={state['Feedback'].code}
                title={state['Feedback'].title}
                text={state['Feedback'].text}
                hidden={state['Feedback'].hidden}
            ></Feedback>
          </Form>
        </main>
    );
}